.panel-wrapper
{
    width: 100%;
    height: 96%;
}
/* ===== Sidebar ===== */
@media screen and (max-width: 450px) {
    .sidebar {
        position: fixed;
        left: 0;
        height: 96%;
        width: 88px;
        padding: 10px 14px;
        background: var(--sidebar-color);
        transition: var(--tran-05);
        z-index: 100;
    }
    .sidebar header .toggle{
        display: none;
    }
    .sidebar.close .lower-group{
        margin-top: 500%;
     }
     .sidebar.close .text{
        opacity: 0;
    }
  }
  @media screen and (min-width: 450px) {
    .sidebar {
        position: fixed;
        left: 0;
        height: 96%;
        width: 250px;
        padding: 10px 14px;
        background: var(--sidebar-color);
        transition: var(--tran-05);
        z-index: 100;
    }
    .sidebar.close {
        width: 88px;
     }
     .sidebar header .toggle{
        position: absolute;
        top: 50%;
        right: -25px;
        transform: translateY(-50%) rotate(180deg);
        height: 25px;
        width: 25px;
        background-color: var(--color-sec1);
        color: var(--sidebar-color);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 22px;
        cursor: pointer;
        transition: var(--tran-05);
    }
    .sidebar .lower-group{
        margin-top: 28%;
    }
    .sidebar.close .toggle{
        transform: translateY(-50%) rotate(0deg);
    }
    .sidebar.close .lower-group{
        margin-top: 430%;
     }
  }

/* ===== Reusable code - Here ==== = */
.sidebar li{
    height: 26px;
    list-style: none;
    display: flex;
    align-items: center;
    margin-top: 5px;
}
.sidebar header .image,
.sidebar .icon {
    min-width: 60px;
    border-radius: 6px;
}

.sidebar .icon{
    min-width: 60px;
    border-radius: 6px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.sidebar .text,
.sidebar .icon{
    color: var(--text-color);
    transition: var(--tran-03);
}

.sidebar .text{
    font-size: 17px;
    font-weight: 500;
    white-space: nowrap;
    opacity: 1;
}
.sidebar.close .text{
    opacity: 0;
}
/* =========================== */

.sidebar header{
    position: relative;
}

.sidebar header .image-text{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.sidebar header .logo-text{
    display: flex;
    flex-direction: column;
}
.sidebar header .logo-text-clip {
    display: inline-block;
    font-size: small;
    white-space: nowrap;
    text-align: center;

}
.sidebar header .logo-text-clip:hover {
    overflow: visible;
}
header .image-text .name {
    margin-top: 2px;
    font-size: 18px;
    font-weight: 600;
}

header .image-text .profession{
    display: inline-block;
    font-size: medium;
    font-weight: bold;
    white-space: nowrap;
    text-align: center;
    margin-top: -2px;
   
}

.sidebar header .image{
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebar header .image img{
    width: 90%;
    border-radius: 50%;
}

div.dark__background .sidebar header .toggle{
    color: var(--text-color);
}
.sidebar .menu-bar{
    margin-top: 10%;
}

.sidebar .menu{
    margin-top: 5px;
}

.sidebar li a{
    list-style: none;
    height: 100%;
    background-color: transparent;
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    border-radius: 6px;
    text-decoration: none;
    transition: var(--tran-03);
}

.sidebar li a:hover{
    background-color: var(--color-grey);
}
.sidebar li a:hover .icon,
.sidebar li a:hover .text{
    color: var(--sidebar-color);
}
.sidebar li a.active .icon,
.sidebar li a.active .text{
    color: var(--color-white);
}
.sidebar li a.active{
    background-color: var(--color-sec1);
    border: 2px solid var(--color-sec1);
    border-radius: 5px;
}
div.dark__background .sidebar li a:hover .icon,
div.dark__background .sidebar li a:hover .text{
    color: var(--text-color);
}
.mode-text.text-low{
    text-align: center;
    font-size: 17px;
    font-weight: 500;
    white-space: nowrap;
    color: var(--text-color);
    transition: var(--tran-03);
    opacity: 1;
}
.menu-bar::-webkit-scrollbar{
    display: none;
}
.sidebar .menu-bar .mode{
    border-radius: 6px;
    background-color: var(--primary-color-light);
    position: relative;
    transition: var(--tran-05);
}

.menu-bar .mode .sun-moon{
    height: 30px;
    width: 30px;
}

.mode .sun-moon i{
    position: absolute;
}
.mode .sun-moon i.sun{
    opacity: 0;
}
div.dark__background .mode .sun-moon i.sun{
    opacity: 1;
}
div.dark__background .mode .sun-moon i.moon{
    opacity: 0;
}

.menu-bar .lower-group .toggle-switch{
    position: absolute;
    right: 42%;
    height: 40px;
    min-width: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 6px;
    cursor: pointer;
}
.sidebar.close .lower-group .toggle-switch{
    right: 17%;
}
.toggle-switch .switch{
    position: relative;
    height: 22px;
    width: 40px;
    border-radius: 25px;
    background-color: var(--toggle-color);
    transition: var(--tran-05);
}
.toggle-switch input {
    opacity: 0;
    width: 0;
    height: 0;
}
.switch::before {
    content: "";
    position: absolute;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
    background-color: var(--sidebar-color);
    transition: var(--tran-04);
}

div.dark__background .switch::before{
    left: 20px;
}

.home{
    position: absolute;
    left: 250px;
    height: 96%;
    padding: 0 10px;
    width: calc(100% - 250px);
    background-color: var(--body-color);
    transition: var(--tran-05);
}
.home .text{
    font-size: 30px;
    font-weight: 500;
    color: var(--text-color);
    padding: 12px 60px;
}
.home .chat-icon
{
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    right: 10px;
    bottom: 15px;
    width: 80px;
    height: 30px;
    border-radius: 6px;
    background-color: var(--color-sec1);
    color: var(--color-white);
}
.home .chat-icon .icon
{
    width: 30px;
    height: 20px;
}

.sidebar.close ~ .home{
    padding: 0 10px;
    left: 88px;
    height: 96%;
    width: calc(100% - 88px);
}
div.dark__background .home .text{
    color: var(--text-color);
}